body {
  font-size: 18px;
}

.breed-select {
  width: 300px;
  height: 50px;
  border-radius: 10px;
  margin-bottom: 20px;
  display: block;
}

.cat-info {
  max-width: 1270px;
  border: 2px solid rgba(137, 186, 208, .11);
  border-radius: 10px;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  display: flex;
  position: relative;
  overflow: hidden;
  box-shadow: 0 0 50px rgba(31, 31, 32, .1);
}

.cat-info__img {
  width: 50%;
}

.cat-info__text-side {
  width: 50%;
  padding: 30px;
}

.cat-info__title {
  font-size: 42px;
}

.loader {
  width: 48px;
  height: 48px;
  box-sizing: border-box;
  border-top: 4px solid #d0e1f1;
  border-right: 4px solid rgba(0, 0, 0, 0);
  border-radius: 50%;
  animation: rotation 1s linear infinite;
  display: inline-block;
}

.loader:after {
  content: "";
  box-sizing: border-box;
  width: 48px;
  height: 48px;
  border-bottom: 4px solid rgba(0, 0, 0, 0);
  border-left: 4px solid #5c84ad;
  border-radius: 50%;
  animation: rotation .5s linear infinite reverse;
  position: absolute;
  top: 0;
  left: 0;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

.is-hiden {
  display: none;
}

